import React from 'react';
import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import {
	Checkbox,
	Table,
	TableBody,
	TableCell,
	TableContainer,
	TableRow,
	Alert,
	useTheme,
	IconButton,
} from '@material-ui/core';
import EditTwoToneIcon from '@material-ui/icons/EditTwoTone';

import { makeStyles } from '@material-ui/core/styles';
import { useQuery, gql } from '@apollo/client';
import { useHistory } from 'react-router';
import { TOffer } from '../../../types';
import { listOffers } from '../../../graphql/queries';

import { getComparator, stableSort } from '../../../utils/table';
import Loader from '../../../ui-component/Loader';
import Header from './ListHeader';
import LoadMore from '../components/LoadMore';
import config from '../../../config';
import { useSelectRow, useOrder } from '../../../hooks';
import { getOfferPrice } from '../../../helpers/offer';
import { formatCurrency } from '../../../helpers/currency';
import OfferToolbar from './OfferToolbar';

const useStyles = makeStyles(() => ({
	table: {
		minWidth: '750px',
	},
}));

const OfferList = () => {
	const { loading, error, data, fetchMore } = useQuery(gql(listOffers), {
		variables: { limit: config.pagination.limit },
		fetchPolicy: 'cache-first',
		errorPolicy: 'all',
		notifyOnNetworkStatusChange: true,
	});

	const rows: TOffer[] = _get(data, 'listOffers.items', []);
	const nextToken: string | null | undefined = _get(
		data,
		'listOffers.nextToken',
		undefined
	);
	const theme = useTheme();
	const classes = useStyles();
	const history = useHistory();
	const { order, orderBy, handleRequestSort } = useOrder('OfferNo');
	const { selected, isSelected, handleSelect, handleSelectAll } =
		useSelectRow('OfferId');

	if (loading && !data) return <Loader />;
	if (error && _isEmpty(data)) {
		return (
			<Alert severity='error' sx={{ color: theme.palette.error.main }}>
				Something went wrong!
			</Alert>
		);
	}

	const loadMore = () => {
		fetchMore({
			variables: { limit: config.pagination.limit, nextToken },
		});
	};

	const handleSelectAllClick = (event: React.ChangeEvent<HTMLInputElement>) => {
		handleSelectAll(event.target.checked, rows);
	};

	const editOffer = (id: string) => {
		history.push(`/offer/${id}`);
	};

	return (
		<>
			<OfferToolbar numSelected={selected.length} />
			<TableContainer>
				<Table className={classes.table} aria-labelledby='tableTitle' size='small'>
					<Header
						numSelected={selected.length}
						order={order}
						orderBy={orderBy}
						onSelectAllClick={handleSelectAllClick}
						onRequestSort={handleRequestSort}
						rowCount={rows.length}
					/>
					<TableBody>
						{stableSort<TOffer>(rows, getComparator(order, orderBy)).map(
							(row, index) => {
								if (typeof row === 'number') return null;
								const isItemSelected = isSelected(row.OfferId);
								const labelId = `enhanced-table-checkbox-${index}`;

								return (
									<TableRow
										hover
										onClick={() => handleSelect(row.OfferId)}
										role='checkbox'
										aria-checked={isItemSelected}
										tabIndex={-1}
										key={row.OfferId}
										selected={isItemSelected}
									>
										<TableCell sx={{ pl: 3 }} padding='checkbox'>
											<Checkbox
												color='primary'
												checked={isItemSelected}
												inputProps={{
													'aria-labelledby': labelId,
												}}
											/>
										</TableCell>
										<TableCell component='th' id={labelId} scope='row' padding='none'>
											{row.OfferId}
										</TableCell>
										<TableCell component='th' id={labelId} scope='row' padding='none'>
											{row.OfferNo}
										</TableCell>
										<TableCell align='left'>{row.PropertyData?.FullAddress}</TableCell>
										<TableCell align='left'>{row.PropertyData?.OwnerNames}</TableCell>
										<TableCell align='left'>{getOfferPrice(row)}</TableCell>
										<TableCell align='left'>{row.Funding?.PurchaseType}</TableCell>
										<TableCell align='left'>{row.Funding?.LoanType}</TableCell>
										<TableCell align='left'>
											{formatCurrency(row.Funding?.DownPayment)}
										</TableCell>
										<TableCell sx={{ pr: 3 }} align='left'>
											{row.Step?.name}
										</TableCell>
										<TableCell align='center' sx={{ pr: 3 }}>
											<IconButton color='primary' onClick={() => editOffer(row.OfferId)}>
												<EditTwoToneIcon sx={{ fontSize: '1.3rem' }} />
											</IconButton>
										</TableCell>
									</TableRow>
								);
							}
						)}
					</TableBody>
				</Table>
			</TableContainer>
			<LoadMore onLoadMore={loadMore} disabled={!nextToken} loading={loading} />
		</>
	);
};

export default OfferList;
