import React from 'react';
import {
	Checkbox,
	TableCell,
	TableHead,
	TableSortLabel,
	TableRow,
} from '@material-ui/core';

import { makeStyles } from '@material-ui/core/styles';
import { visuallyHidden } from '@material-ui/utils';
import { EnhancedTableHeadProps, HeadCell } from '../../../types';

const headCells: HeadCell[] = [
	{
		id: 'OfferId',
		numeric: false,
		disablePadding: true,
		label: 'ID',
	},
	{
		id: 'OfferNo',
		numeric: false,
		disablePadding: true,
		label: 'No',
	},
	{
		id: 'PropertyData.FullAddress',
		numeric: false,
		disablePadding: false,
		label: 'Property Address',
	},
	{
		id: 'PropertyData.OwnerNames',
		numeric: false,
		disablePadding: false,
		label: 'Sellers',
	},
	{
		id: 'CounterOffer.SellerCounter.OfferPrice',
		numeric: true,
		disablePadding: false,
		label: 'Offer Price',
	},
	{
		id: 'Funding.PurchaseType',
		numeric: false,
		disablePadding: false,
		label: 'Funding',
	},
	{
		id: 'Funding.LoanType',
		numeric: false,
		disablePadding: false,
		label: 'Loan Type',
	},
	{
		id: 'Funding.DownPayment',
		numeric: false,
		disablePadding: false,
		label: 'Down Payment',
	},
	{
		id: 'Step.messages.advisor',
		numeric: false,
		disablePadding: false,
		label: 'Status',
	},
];

const useStyles = makeStyles(() => ({
	sortSpan: { ...visuallyHidden },
	OfferId: {
		width: '170px',
	},
}));

const ListHeader = (props: EnhancedTableHeadProps) => {
	const classes = useStyles();
	const {
		onSelectAllClick,
		order,
		orderBy,
		numSelected,
		rowCount,
		onRequestSort,
	} = props;
	const createSortHandler =
		(property: string) => (event: React.SyntheticEvent) => {
			onRequestSort(event, property);
		};

	const getClassName = (prop: string) => (classes as any)[prop] || '';

	return (
		<TableHead>
			<TableRow>
				<TableCell padding='checkbox' sx={{ pl: 3 }}>
					<Checkbox
						color='primary'
						indeterminate={numSelected > 0 && numSelected < rowCount}
						checked={rowCount > 0 && numSelected === rowCount}
						onChange={onSelectAllClick}
						inputProps={{
							'aria-label': 'select all desserts',
						}}
					/>
				</TableCell>
				{headCells.map(headCell => (
					<TableCell
						key={headCell.id}
						align={headCell.numeric ? 'right' : 'left'}
						padding={headCell.disablePadding ? 'none' : 'normal'}
						sortDirection={orderBy === headCell.id ? order : undefined}
						className={getClassName(headCell.id)}
					>
						<TableSortLabel
							active={orderBy === headCell.id}
							direction={orderBy === headCell.id ? order : 'asc'}
							onClick={createSortHandler(headCell.id)}
						>
							{headCell.label}
							{orderBy === headCell.id ? (
								<span className={classes.sortSpan}>
									{order === 'desc' ? 'sorted descending' : 'sorted ascending'}
								</span>
							) : null}
						</TableSortLabel>
					</TableCell>
				))}
				<TableCell sortDirection={false} align='center' sx={{ pr: 3 }}>
					Action
				</TableCell>
			</TableRow>
		</TableHead>
	);
};

export default ListHeader;
