import { Grid, Button, CircularProgress } from '@material-ui/core';
import { IconChevronDown } from '@tabler/icons';
import { makeStyles, withStyles } from '@material-ui/core/styles';

const LoadMoreButton = withStyles({
	root: {
		borderRadius: '50%',
		minWidth: '0',
		padding: '12px',
	},
})(Button);

type Props = {
	onLoadMore: Function;
	loading: boolean;
	disabled: boolean;
};

const useStyles = makeStyles(() => ({
	toolbar: {
		padding: '24px 16px 2px',
	},
}));

const LoadMore = ({ onLoadMore, disabled, loading }: Props) => {
	const classes = useStyles();

	if (loading) {
		return (
			<Grid container className={classes.toolbar} justifyContent='center'>
				<CircularProgress />
			</Grid>
		);
	}

	return (
		<Grid container className={classes.toolbar} justifyContent='center'>
			<LoadMoreButton
				type='button'
				onClick={() => onLoadMore()}
				disabled={disabled}
			>
				<IconChevronDown />
			</LoadMoreButton>
		</Grid>
	);
};

export default LoadMore;
