import _get from 'lodash.get';
import { KeyedObject, ArrangementOrder } from '../types';

const descendingComparator = (
	a: KeyedObject,
	b: KeyedObject,
	orderBy: string
) => {
	if (_get(b, orderBy) < _get(a, orderBy)) {
		return -1;
	}
	if (_get(b, orderBy) > _get(a, orderBy)) {
		return 1;
	}
	return 0;
};

const getComparator = (order: ArrangementOrder, orderBy: string) =>
	order === 'desc'
		? (a: KeyedObject, b: KeyedObject) => descendingComparator(a, b, orderBy)
		: (a: KeyedObject, b: KeyedObject) => -descendingComparator(a, b, orderBy);

const stableSort = <T>(
	array: T[],
	comparator: (a: KeyedObject, b: KeyedObject) => number
) => {
	const stabilizedThis = array.map((el, index) => [el, index]);
	stabilizedThis.sort((a, b) => {
		const order = comparator(a[0] as T, b[0] as T);
		if (order !== 0) return order;
		return (a[1] as number) - (b[1] as number);
	});
	return stabilizedThis.map(el => el[0]);
};

export { descendingComparator, getComparator, stableSort };
